import React, { useState, useCallback, useEffect } from 'react';
import { useDropzone } from 'react-dropzone';
import axios from 'axios';
import Slider from 'react-slick';
import { Range, getTrackBackground } from 'react-range';

const UploadPage = () => {
  const [files, setFiles] = useState([]);
  const [texts, setTexts] = useState([]);
  const [file, setFile] = useState(null);
  const [preview, setPreview] = useState(null);
  const [totalDuration, setTotalDuration] = useState(0);
  const [isVideoOpen, setIsVideoOpen] = useState(false);
  const [isTextOpen, setIsTextOpen] = useState(false);
  const [isImageOpen, setIsImageOpen] = useState(false);
  const [isMusicOpen, setIsMusicOpen] = useState(false);
  const [code, setCode] = useState("");
  const [image, setImage] = useState([]);
  const [newImage, setNewImage] = useState(null);
  const [music, setMusic] = useState("");
  const [newMusic, setNewMusic] = useState(null);
  const [addText, setAddText] = useState("");
  const [addStyle, setAddStyle] = useState("1");
  const [selectedFile, setSelectedFile] = useState(null);
  const [values, setValues] = useState([0, 0]);
    
  const onDrop = useCallback((acceptedFiles) => {
    const selectedFile = acceptedFiles[0];
    setSelectedFile(selectedFile)
    if (selectedFile && selectedFile.type === 'video/mp4') {
      setFile(
        Object.assign(selectedFile, {
          preview: URL.createObjectURL(selectedFile),
        })
      );
    } else {
      alert('Please select an MP4 file');
    }
  }, []);

  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
    accept: 'video/mp4',
    maxFiles: 1,
  });

  useEffect(() => {
    if(isVideoOpen === false){
      setFile(null)
    }
  }, [isVideoOpen])

  useEffect(() => {
    if(isTextOpen === false){
      setAddText("");
      setAddStyle("1");
    }
  }, [isTextOpen])

  useEffect(() => {
    if (files.length > 0) {
      const durations = [];
      files.forEach((file, index) => {
        const video = document.createElement('video');
        video.src = `/${file}`;
        video.onloadedmetadata = () => {
          durations.push(video.duration);
          if (durations.length === files.length) {
            const total = durations.reduce((a, b) => a + b, 0);
            setTotalDuration(total);
            setValues([0, total]);
          }
        };
      });
    }
  }, [files]);
  
  const handleUpload = async () => {
    const formData = new FormData();
    formData.append('file', selectedFile);
    try {
      // Using axios
      const response = await axios.post('/api/uploads', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      if (response.status === 200) {
        console.log(response.data["filename"])
        setIsVideoOpen(false)
        setFiles([...files, response.data["filename"]])
      } else {
        alert('Failed to upload file');
      }
    } catch (error) {
      console.error('Error uploading file:', error);
    }
  };

  useEffect(() => {
    if(isImageOpen === false){
      setNewImage(null);
    }
  }, [isImageOpen])

  useEffect(() => {
    if(isMusicOpen === false){
      setNewMusic(null);
    }
  }, [isMusicOpen])

  const handleUploadEtc = async () => {
    const formData = new FormData();
    if(isImageOpen === true){
      formData.append('file', newImage);
      try {
        // Using axios
        const response = await axios.post('/api/uploads', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });
        if (response.status === 200) {
          console.log(response.data["filename"])
          setIsImageOpen(false)
          setNewImage(null)
          setImage([...image, {
            "file": response.data["filename"],
            "start_time": (values[0]).toFixed(2),
            "duration": (values[1] - values[0]).toFixed(2) 
          }])
        } else {
          alert('Failed to upload file');
        }
      } catch (error) {
        console.error('Error uploading file:', error);
      }
    } else if (isMusicOpen === true) {
      formData.append('file', newMusic);
      try {
        // Using axios
        const response = await axios.post('/api/uploads', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });
        if (response.status === 200) {
          console.log(response.data["filename"])
          setIsMusicOpen(false)
          setNewMusic(null)
          setMusic(response.data["filename"])
        } else {
          alert('Failed to upload file');
        }
      } catch (error) {
        console.error('Error uploading file:', error);
      }
    }
  };

  const handleGenerate = async () => {
    let data = {
      "videos": files,
      "text": texts,
      "images": image,
      "music": music,
      "duration": totalDuration,
      "compression": "4M",
      "format": "1920x1080"
    }

    let response = await axios.post("/api/render", {
      "videos": files,
      "text": texts,
      "images": image,
      "music": music,
      "duration": totalDuration.toString(),
      "compression": "4M",
      "format": "1920x1080"
    })

    console.log(response)
    if (response.status === 200) {
      console.log(response.data["output_filename"])
      setPreview(`/${response.data["output_filename"]}`);
      window.open(`/${response.data["output_filename"]}`, "_blank")
    }

    console.log(JSON.stringify(data))
  }

  const handleShowAPI = () => {
    let data = {
      "videos": files,
      "text": texts,
      "images": image,
      "music": music,
      "duration": totalDuration,
      "compression": "4M",
      "format": "1920x1080"
    }
    setCode(JSON.stringify(data, null, 4))
  }

  const handleAddText = (event) => {
    setTexts([...texts, {
      content: addText,
      style: addStyle,
      start_time: (values[0]).toFixed(2),
      duration: (values[1] - values[0]).toFixed(2)
    }])
    console.log(texts)
    setIsTextOpen(false)
  };
  
  const handlePreview = (file) => {
    setPreview(file);
  };

  const sliderSettings = {
    dots: true,
    infinite: false,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    adaptiveHeight: true,
  };

  return (
    <div className='min-h-screen bg-gray-900 flex justify-center'>
      <div className='mt-10 w-2/5 mx-auto'>
        <div className="bg-gray-800 shadow-md rounded-lg p-6">
            <h2 className="text-2xl font-bold mb-4 text-white">CRAFT YOUR MASTERPIECE</h2>
            <div className="mb-4 border-dashed border-2 border-gray-400 rounded-md h-72 mx-6 flex items-center justify-center">
              {preview ? (
                  <video src={preview} controls className="w-full h-full object-cover" />
              ) : (
                  <p className="text-gray-300">Preview will be shown here</p>
              )}
            </div>
            <div className="mb-4 px-4">
              <Slider {...sliderSettings}>
                  {files.map((file) => (
                    <div className="relative px-2 h-24">
                      <video
                        src={`/${file}`}
                        onClick={() => handlePreview(`/${file}`)}
                        className="w-full h-24 object-cover cursor-pointer rounded"
                        muted
                      />
                    </div>
                  ))}
                  <button key="Add Video" onClick={() => setIsVideoOpen(true)} className="border-dashed border-2 border-gray-400 rounded-md h-24 mx-6 flex items-center justify-center text-gray-100">
                    <p>Add Video</p>
                  </button>
              </Slider>
            </div>
            <div className="mb-4 px-4">
              {totalDuration > 0 && (
                <div className="mt-4">
                <Range
                  values={values}
                  step={0.1}
                  min={0}
                  max={totalDuration}
                  onChange={e => setValues(e)}
                  renderTrack={({ props, children }) => (
                  <div
                    {...props}
                    style={{
                      ...props.style,
                      height: '6px',
                      width: '100%',
                      background: getTrackBackground({
                        values: values,
                        colors: ['#ccc', '#548BF4', '#ccc'],
                        min: 0,
                        max: totalDuration
                      })
                    }}
                  >
                    {children}
                  </div>
                  )}
                  renderThumb={({ props }) => (
                  <div
                    {...props}
                    style={{
                      ...props.style,
                      height: '16px',
                      width: '4px',
                      backgroundColor: '#FFF',
                      border: '1px solid #548BF4',
                      borderBottom: '2px solid #346BD4'
                    }}
                  />
                  )}
                />
                </div>
              )}
              {totalDuration > 0 && (
                <div className="flex justify-between text-sm mt-2 text-gray-200">
                <span>{values[0].toFixed(2)}s</span>
                <span>{values[1].toFixed(2)}s</span>
                </div>
              )}
            </div>
            <div className="mb-4 px-4">
              <Slider {...sliderSettings} className='mb-4 p-2'>
                {texts.map((text) => (
                  <div className="relative px-2">
                    <div className='bg-gray-900 shadow-md shadow-black/4 rounded-md h-16 mx-6 flex items-center justify-center text-gray-100'>
                      <p>{text.content}</p>
                    </div>
                  </div>
                ))}
                <button key="Add Video" onClick={() => setIsTextOpen(true)} className="border-dashed border-2 border-gray-400 rounded-md h-16 mx-6 flex items-center justify-center text-gray-100">
                  <p>Add Text</p>
                </button>
              </Slider>
            </div>
            <div className='flex justify-end gap-4'>
              <button
                onClick={() => setIsImageOpen(true)}
                className="w-32 bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-600 "
              >
                Add Logo
              </button>
              <button
                onClick={() => setIsMusicOpen(true)}
                className="w-32 bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-600 "
              >
                Add Music
              </button>
              <button
                onClick={handleShowAPI}
                className="w-32 bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-600 "
              >
                Show API
              </button>
              <button
                onClick={handleGenerate}
                className="w-32 bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-600 "
              >
                Create Video
              </button>
            </div>
            {isVideoOpen && (
              <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
                <div className="bg-gray-800 p-6 rounded shadow-lg">
                  <h2 className="text-xl mb-4 text-gray-100">Add New Video</h2>
                  <div className='mb-4'>
                    {
                      file == null ?
                      <div
                      {...getRootProps()}
                      style={{
                        border: '2px dashed #0087F7',
                        borderRadius: '5px',
                        padding: '20px',
                        textAlign: 'center',
                        width: '100%',
                        maxHeight: '300px',
                      }}
                      >
                        <input {...getInputProps()} />
                          <p className='text-gray-100'>Drag 'n' drop an MP4 file here, or click to select one</p>
                        </div>
                      : 
                      <div style={{ marginTop: 16 }}>
                          <video
                            src={file.preview}
                            controls
                            style={{ width: '100%', maxHeight: '300px' }}
                            />
                        </div>
                    }
                  </div>
                  <div>
                    <button disabled={file == null} type="button" className={`mt-4 px-4 py-2 rounded ${file ? 'bg-indigo-600 hover:bg-indigo-700 text-white' : 'bg-gray-400 text-gray-700 cursor-not-allowed'}`} onClick={handleUpload}>Upload</button>
                    <button type="button" className="rounded bg-red-600 mx-4 px-4 py-2 text-white shadow-sm hover:bg-red-500" onClick={() => setIsVideoOpen(false)}>Close</button>
                  </div>
                </div>
              </div>
            )}

            {isImageOpen && (
              <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
                <div className="bg-gray-800 p-6 rounded shadow-lg">
                  <h2 className="text-xl mb-4 text-gray-100">Add New Logo</h2>
                  <div className='mb-4'>
                    <input type="file" onChange={e => setNewImage(e.target.files[0])} />
                  </div>
                  <div className='mb-4 text-gray-100'>
                    <p>Start: {values[0].toFixed(2)}s</p>
                    <p>Duration: {(values[1] - values[0]).toFixed(2)}s</p>
                  </div>
                  <div>
                    <button disabled={newImage == null} type="button" className={`mt-4 px-4 py-2 rounded ${newImage ? 'bg-indigo-600 hover:bg-indigo-700 text-white' : 'bg-gray-400 text-gray-700 cursor-not-allowed'}`} onClick={handleUploadEtc}>Add Logo</button>
                    <button type="button" className="rounded bg-red-600 mx-4 px-4 py-2 text-white shadow-sm hover:bg-red-500" onClick={() => setIsImageOpen(false)}>Close</button>
                  </div>
                </div>
              </div>
            )}

            {isMusicOpen && (
              <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
                <div className="bg-gray-800 p-6 rounded shadow-lg">
                  <h2 className="text-xl mb-4 text-gray-100">Add New Logo</h2>
                  <div className='mb-4'>
                    <input type="file" onChange={e => setNewMusic(e.target.files[0])} />
                  </div>
                  <div>
                    <button disabled={newMusic == null} type="button" className={`mt-4 px-4 py-2 rounded ${newMusic ? 'bg-indigo-600 hover:bg-indigo-700 text-white' : 'bg-gray-400 text-gray-700 cursor-not-allowed'}`} onClick={handleUploadEtc}>Add Music</button>
                    <button type="button" className="rounded bg-red-600 mx-4 px-4 py-2 text-white shadow-sm hover:bg-red-500" onClick={() => setIsMusicOpen(false)}>Close</button>
                  </div>
                </div>
              </div>
            )}

            {isTextOpen && (
              <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
                <div className="bg-gray-800 p-6 rounded shadow-lg">
                  <h2 className="text-xl mb-4 text-gray-100">Add Text Video</h2>
                  <div className='mb-4'>
                    <label htmlFor="fontStyle" className="block mb-2 text-gray-200">Animation Style</label>
                    <select
                      id="fontStyle"
                      className="p-2 rounded bg-gray-700 text-white"
                      value={addStyle}
                      onChange={e => setAddStyle(e.target.value)}
                    >
                      <option value="1">Style 1</option>
                      <option value="2">Style 2</option>
                      <option value="3">Style 3</option>
                    </select>
                  </div>
                  <div className='mb-4'>
                    <label htmlFor="newText" className="block mb-2 text-gray-200">Text</label>
                    <input
                      id="newText" 
                      class="min-w-0 flex-auto rounded-md border-0 bg-white/5 px-3.5 py-2 text-white shadow-sm ring-1 ring-inset ring-white/10 focus:ring-2 focus:ring-inset focus:ring-indigo-500 sm:text-sm sm:leading-6"
                      placeholder="Add New Text"
                      required
                      value={addText}
                      onChange={e => setAddText(e.target.value)}
                    />
                  </div>
                  <div className='mb-4 text-gray-100'>
                    <p>Start: {values[0].toFixed(2)}s</p>
                    <p>Duration: {(values[1] - values[0]).toFixed(2)}s</p>
                  </div>
                  <div>
                    <button disabled={addText === ""} type="button" className={`mt-4 px-4 py-2 rounded ${addText !== "" ? 'bg-indigo-600 hover:bg-indigo-700 text-white' : 'bg-gray-400 text-gray-700 cursor-not-allowed'}`} onClick={handleAddText}>Add Text</button>
                    <button type="button" className="rounded bg-red-600 mx-4 px-4 py-2 text-white shadow-sm hover:bg-red-500" onClick={() => {setIsTextOpen(false); setAddText("")}}>Close</button>
                  </div>
                </div>
              </div>
            )}
          <div className='w-full align-left, text-left text-gray-100 p-2 bg-gray-900 mt-4 scroll'>
            <pre>
              {
                code
              }
            </pre>
          </div>
          </div>
      </div>
    </div>
  );
};

export default UploadPage;
